.csm_s {
       margin-right: 180px;
       max-width: 350px;
}

.turst_app{
       transition: all .2s ease-out;
}
.turst_app:hover{
       box-shadow: 0 1px 10px #0003;
}
.add_feed_{
       min-height: calc(-240px + 100vh);
       max-width: 52rem;
}
.onboard_step .nav-link.active{
       color: var(--bs-primary);
}
.onboard_step .nav-link.active .rounded-circle{
       transition: all .3s ease;
}
.onboard_step .nav-link.active .rounded-circle{
       border-color: var(--bs-primary) !important;
}
.onboard__ .card-header .nav-item{
       position: relative;
}

.onboard__ .card-header .nav-item:last-child:after{
       background-image: none;
}

.onboard__ .nav-link:hover, .onboard__  .nav-link:focus{
       color: var(--bs-nav-link-color);
}
.onboard__ .nav-underline .nav-link.active, 
.onboard__ .nav-underline .show > .nav-link {
       color: var(--bs-primary);
}

.onboard__ .onboard_tabs_{
       min-height: calc(-375px + 100vh);
}

.onboard__ .add_feed_{
       max-width: 52rem;
       margin: 0 auto;
       justify-content: center;
       padding: 3% 0;
}

.onboard__ .onboard_tabs_,
.onboard__ .add_feed_{
       min-height: calc(-375px + 100vh);
}
@media screen and (max-width: 767px) {
       .csm_s {
              margin-right: 0;
              max-width: 100%;
              margin-bottom: 30px !important;
       }

       .csm_profile {
              right: -20px !important;
       }

       .csm_profile img {
              max-width: 130px;
       }
}

@media screen and (max-width: 575px) {
       .csm_profile img {
              max-width: 100px;
       }
       .onboard__ .nav-underline{
              width: 100%;
       }
       .onboard__ .nav-underline .nav-link{
              justify-content: start;
       }
}

@media screen and (min-width: 767px) {
       .onboard__ .card-header .nav-item:after{
              content: "";
              background-image: url("data:image/svg+xml,%3Csvg width='9' height='17' viewBox='0 0 9 17' fill='none' xmlns='http://www.w3.org/2000/svg'%3E%3Cpath d='M0.352255 16.4961C0.28339 16.4962 0.21604 16.4759 0.158743 16.4377C0.101445 16.3995 0.0567804 16.3451 0.0304106 16.2815C0.00404084 16.2179 -0.00284656 16.1479 0.0106215 16.0803C0.0240895 16.0128 0.0573065 15.9508 0.106062 15.9022L7.51066 8.49825L0.106062 1.09437C-0.0299123 0.958394 -0.0299123 0.737954 0.106062 0.60198C0.242036 0.466007 0.462476 0.466007 0.598449 0.60198L8.24856 8.25209C8.3843 8.38787 8.3843 8.60798 8.24856 8.74379L0.598449 16.3939C0.566158 16.4263 0.527789 16.452 0.485542 16.4695C0.443295 16.4871 0.398 16.4961 0.352255 16.4961Z' fill='%236D7175'/%3E%3C/svg%3E%0A");       
              position: absolute;
              right: -30px;
              top: 18px;
              height: 18px;
              width: 10px;
              background-repeat: no-repeat;
       }
}
@media screen and (min-width: 576px) {
.onboard__ .onboard_tabs_,
.onboard__ .add_feed_{
       min-height: calc(-250px + 100vh);
}
}